.testimonials {
  background-color: $main-background-color;
  padding: 120px 20px;

  @media (min-width: 640px) {
    padding: 148px 40px;
  }

  &__img {
    position: relative;
    width: 110px;
    height: 110px;
    object-fit: cover;
    border-radius: 50%;
    z-index: 1;

    &-block {
      z-index: 0;
      width: 109px;
      height: 44px;
      background-color: $befor-hire-color;
      border-radius: 12px;
      transform: rotate(-60deg);
      position: relative;

      &--1 {
        top: 40px;
        right: 40px;
      }

      &--2 {
        bottom: 40px;
        left: 40px;
      }
    }
  }

  &__card {
    padding-top: 35px;
    padding-bottom: 32px;
    background-color: $nav-link-color;
    border-radius: 16px;
    box-shadow: 0px 9px 18px rgba(37, 41, 49, 0.03);
    display: grid;
    justify-items: center;

    @media (min-width: 1024px) {
      max-width: 362px;
    }

    &-container {
      display: grid;
      gap: 30px;

      @media (min-width: 1024px) {
        grid-template-columns: 1fr 1fr 1fr;
        max-width: 1146px;
        margin: 0 auto;
      }
    }
  }

  &__discription {
    @extend %services-card__discription;
    text-align: center;
    padding: 0 42px;
    padding-top: 11px;
    max-width: 362px;

    @media(min-width: 640px) {
      padding-top: 18px;
    }
  }

  &__name {
    @extend %testi__name;
    padding-top: 32px;

    @media(min-width: 640px) {
      padding-top: 16px;
    }
  }

  &__job {
    padding-top: 4px;
  }

  &__title {
    @extend %title__small;
    padding-top: 16px;
    padding-bottom: 48px;
  }
}
