.services {
  background-color: $main-background-color;
  padding: 120px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: 640px) {
    padding: 148px 40px;
  }

  @media (min-width: 1024px) {
    gap: 50px;
    flex-direction: row;
    justify-content: center;
  }

  &__title {
    @extend %title__wide;
    padding-top: 16px;

    @media (min-width: 640px){
      width: 50%;
    }
  }

  &__discription {
    @extend %main__discription;
    padding-top: 24px;

    @media (min-width: 640px){
      width: 40%;
    }
  }

  &__work {
    @extend %services-card__discription;
  }

  &__card {
    background-color: $nav-link-color;
    box-shadow: 0px 9px 18px rgba(37, 41, 49, 0.03);
    border-radius: 16px;
    padding: 32px;
    width: 280px;

    @media (min-width: 640px) {
      &:nth-child(even) {
        transform: translateY(-50%);
      }
      width: 264px;
    }

        @media (min-width: 1024px) {
      width: 214px;
    }

    @media (min-width: 1600px) {
      width: 264px;
    }


    &-container {
      display: grid;
      gap: 24px;
      padding-top: 48px;

      @media (min-width: 640px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        max-width: 600px;
      }
    }

    &-num {
      display: grid;
      @extend %services-card__num;
      align-items: center;
      padding-left: 10px;
      padding-bottom: 26px;

      @media (min-width:640px) {
        padding-left: 0;
      }

       &::before {
        content: '';
        position:relative;
        left: -10px;
        bottom: -22px;
        width: 46px;
        height: 24px;
        background-color: $befor-hire-color;
        opacity: 0.1;
        border-radius: 23px;
      }
    }

    &-link {
      @extend %services-card__btn;
    }

    &-button {
      display: grid;
      grid-auto-flow: column;
      align-items: center;
      padding-top: 32px;
      grid-template-columns: 25% 1fr;

      @media (min-width: 1024px) {
        grid-template-columns: 35% 1fr;
      }
    }
  }
}
