.our-expertise {
  padding: 72px 20px;
  background-color: $nav-link-color;

  @media (min-width: 640px) {
    padding: 128px 40px;
  }

  &__title {
    @extend %title;
    text-align: center;
  }

  &__wrapper {
    display: grid;
    padding-top: 56px;
    gap: 56px;

    @media (min-width: 640px) {
      padding-top: 64px;
      gap: 72px;
    }

    @media (min-width: 1024px) {
      grid-template-columns: 1fr 1fr 1fr;
      max-width: 1146px;
      margin: 0 auto;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 16px;

    &-title {
      @extend %cards__title;
      text-align: center;
      padding-top: 16px;
    }

    &-discription {
      @extend %main__discription;
      max-width: 362px;
      margin: 0 auto;
      text-align: center;
    }

    &-img {
      width: 184px;
      height: 166px;
      border-radius: 15px;
    }
  }
}
