.header {
  background-color: $header-footer-background-color;
  border-radius: 0 0 30px 30px;
}

.nav {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 50px 1fr 70px;
  column-gap: 20px;
  padding: 0 20px;
  max-width: 1446px;
  margin: 0 auto;

  @media (min-width: 640px) {
    padding: 0 40px;
  }

  @media (min-width: 1024px) {
    grid-template-columns: 1fr 750px 1fr;
    justify-items: center;
  }

  &__action {
    display: grid;
    margin-top: 52px;
    grid-auto-flow: column;
    justify-content: center;
    column-gap: 48px;


    @media (max-width: 1024px) {
      width: 100%;
      height: 100%;
      position: fixed;
      bottom: 100%;
      left: 0;
      transition: 0.3s;
      background-color: $burger-menu-color;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      gap: 25px;
      z-index: 20;

      &:target {
        transform: translateY(100%);
      }
    }
  }

  &__logo {
    display: inline-block;
    background: url(../images/logo/dia-logo.svg) no-repeat center;
    height: 25px;
    width: 49px;
    margin-top: 19px;
    justify-self: left;

    @media (min-width: 1024px) {
      margin-top: 49px;
    }

    @media (min-width: 1225px) {
      justify-self: auto;
    }
  }

  &__burger {
    display: inline-block;
    background: url(../images/logo/Vector.svg) no-repeat center;
    height: 8px;
    width: 18px;
    margin-top: 32px;

    @media (min-width: 1024px) {
      display: none;
    }
  }

  &__cross-item {
    display: block;
    background: url(../images/logo/cross.svg) no-repeat center;
    width: 18px;
    height: 18px;
    padding: 40px;

    @media (min-width: 1024px) {
      display: none ;
    }
  }

  &__hire {
    @extend %a_nav;
    margin-top: 18px;
    position: relative;
    letter-spacing: 1.5px;
    max-width: 67px;

    &::after {
      content: ' ';
      width: 67px;
      height: 2px;
      border-radius: 4px;
      background-color: $befor-hire-color;
      top: 100%;
      left: -5%;
      position: absolute;
    }

    @media (min-width: 1024px) {
      margin-top: 52px;
    }
  }
}

.content {
  padding: 0 20px;
  padding-bottom: 48px;
  z-index: 10;
  background-color: $header-footer-background-color;
  border-radius: 30px;

  @media (min-width:640px) {
    padding-bottom: 147px;
    padding: 0 40px;
    padding-bottom: 48px;
  }
  //fix header
  @media (min-width: 1024px) {
    max-width: 490px;
  }

  &__discription {
    @extend %p__header;
    width: 362px;
  }

  &__button {
    @extend %btn__header;
  }
}

.slider {
  width: 100%;
  height: 390px;
  position: relative;

  @media (min-width: 1024px) {
    height: 670px;
    padding-top: 40px;
  }

 &__img {
  height: 390px;
  width: 100%;
  border-radius: 30px;
  position: absolute;
  object-fit: cover;

  @media (min-width: 1024px) {
    height: 630px;
  }
 }

 &__btn {
  position: absolute;
  padding: 10px;
  border: none;
  border-radius: 10px;
  cursor: pointer;

  &:active {
    transform: scale(1.1);
  }

  &--prev {
    top: 50%;
    left: 30px;

    @media (min-width: 1024px) {
      top: 85%;
    }
  }

  &--next {
    top: 50%;
    left: 100px;

    @media (min-width: 1024px) {
      top: 85%
    }
  }
 }

 &__intro {
    @extend %p__slider;
    position: absolute;
    left: 30px;
    bottom: 20%;

    @media (min-width: 1024px) {
      bottom: 11%;
      left: 160px;
    }
 }

 &__discription{
    @extend %slider__discription;
    position: absolute;
    left: 30px;
    bottom: 8%;

    @media (min-width: 1024px) {
      left: 160px;
      bottom: 3%;
    }
 }
}

.wrapper {

  @media (min-width: 1024px) {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}
