.who-we-are {
  position: relative;
  z-index: -1;
  margin-top: -22px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 142px 20px 120px;
  gap: 24px;
  background-color: $main-background-color;

  @media (min-width: 640px) {
    padding: 160px 40px 148px;
    gap: 16px;
  }

  &__title {
    @extend %title__small;
    text-align: center;
  }

  &__discription {
    @extend %who-we-are__discription;
    text-align: center;
    max-width: 716px;
    margin: 0 auto;
  }
}
